import React, { Component } from 'react';
import './App.css';
import { Link } from 'react-router-dom';
import { Image } from 'react-bootstrap';

class Home extends Component{

	state = {
		img: require('./images/logonav.png')
	}

	render() {
		return (
			<div id= "homepage">
		  <div className="App">
				<Link to='/life'>
				<img
					src={this.state.img} id="title"
					onMouseEnter={() => {
            this.setState({
              img: require('./images/logo.png')
            })
          }}

          onMouseOut={() => {
            this.setState({
              img: require('./images/logonav.png')
            })
          }}
				/>
				</Link>
			</div>
			</div>
		);
	}
}

export default Home;
